
import fetch from 'isomorphic-unfetch'

const Index = (props) => (
  <div>
    <h1>Hello Next.js</h1>
    <pre><code>{props.shows}</code></pre>
    <a onClick={() => console.log('ja')}>ververs op de client</a>
  </div>
)

Index.getInitialProps = async function() {
  const res = await fetch('http://whoami.docker.localhost/')
  const data = await res.text()

  console.log(data)
  return {
    shows: data
  }
}

export default Index
